import React, { useState, useEffect } from "react";
import "./SkillText.css";

const SCALE_MAX = 5;

const SkillText = ({ skill, scale }) => {
  const items = [];
  for (let i = 0; i < SCALE_MAX; i++) {
    if (i < scale) {
      items.push(<div className="skill-scale-on" key={i} />);
    } else {
      items.push(<div className="skill-scale-off" key={i} />);
    }
  }
  return (
    <div className="skill-container">
      <div className="skill-text">{skill}</div>
      <div>
        <div className="skill-scale-container">{items}</div>
      </div>
    </div>
  );
};

export default SkillText;
