import React, { useState } from "react";
import "./EducationCard.css";

const EducationCard = ({ title, subtitle, logo, year }) => {
  return (
    <div className="card-education-container">
      <img src={logo} alt="logo" className="education-logo" />
      <div className="card-education-right">
        <div className="card-education-title">{title}</div>
        <div className="card-education-subtitle">{subtitle}</div>
        <div className="card-education-year">{year}</div>
      </div>
    </div>
  );
};

export default EducationCard;
