import React from "react";
import "./MainScreen.css";
import HeaderCard from "./../../component/HeaderCard/HeaderCard";
import SkillsCard from "./../../component/SkillsCard/SkillsCard";
import SectionTitle from "./../../component/Text/SectionTitle/SectionTitle";
import CertificationList from "../../component/CertificationList/CertificationList";
import EducationList from "../../component/EducationList/EducationList";
import ExperienceList from "../../component/ExperienceList/ExperienceList";

const MainScreen = () => {
  return (
    <div className="container">
      <HeaderCard />
      <SkillsCard />
      <SectionTitle text="Certifications" />
      <CertificationList />
      <SectionTitle text="Educations" />
      <EducationList />
      <SectionTitle text="Experiences" />
      <ExperienceList />
    </div>
  );
};

export default MainScreen;
