import React, { useState, useEffect } from "react";
import SkillText from "./SkillText/SkillText";
import "./SkillsCard.css";

import chevron from "./../../assets/icon/chevron.svg";

const data = [
  { skill: "HTML", scale: 5 },
  { skill: "CSS", scale: 5 },
  { skill: "Javascript", scale: 4 },
  { skill: "React", scale: 4 },
  { skill: "React Native", scale: 4 },
  { skill: "SASS", scale: 3 },
  { skill: "Laravel", scale: 2 }
];

const SkillsCard = () => {
  const [toggle, setToggle] = useState(false);
  const wrapperRef = React.createRef();
  const chevronRef = React.createRef();
  function handleClick() {
    const wrapper = wrapperRef.current;
    const chevron = chevronRef.current;
    wrapper.classList.toggle("card-skills-open");
    chevron.classList.toggle("chevron-open");
    setToggle(!toggle);
  }

  return (
    <div
      className="card-skills-container"
      onClick={() => handleClick()}
      ref={wrapperRef}
    >
      <div className="card-skills-top">
        <div>Skills</div>
        <img src={chevron} className="chevron" ref={chevronRef} />
      </div>

      <div className="card-skills-content">
        {data.map(({ skill, scale }, index) => {
          return <SkillText key={index} skill={skill} scale={scale} />;
        })}
      </div>
    </div>
  );
};

export default SkillsCard;
