import React, { useState } from "react";
import HorizontalScroll from "react-scroll-horizontal";
import EducationCard from "./EducationCard/EducationCard";
import Binus from "./../../assets/logo/binus.png";
import Sma3 from "./../../assets/logo/sma3.png";
import "./EducationList.css";

const data = [
  {
    title: "Bina Nusantara University",
    subtitle: "Master Track Computer Science",
    year: "Present - 2017",
    logo: Binus,
  },
  {
    title: "SMA 3 Teladan Jakarta",
    subtitle: "Science",
    logo: Sma3,
    year: "2017 - 2014",
  },
];

const EducationList = () => {
  return (
    <div className="education-list-container">
      <HorizontalScroll reverseScroll={true}>
        {data.map((item, index) => {
          return <EducationCard key={index} {...item} />;
        })}
      </HorizontalScroll>
    </div>
  );
};

export default EducationList;
