import React from "react";
import CertificationCard from "./CertificationCard/CertificationCard";
import Hacktiv8 from "./../../assets/logo/hacktiv8.png";
import GoAcademy from "./../../assets/logo/goacademy.png";
import FreeCodeCamp from "./../../assets/logo/freecodecamp.jpeg";
import GoogleDigitalGarage from "./../../assets/logo/google-digital-garage.svg";

const data = [
  {
    text: "React and React Native",
    logo: Hacktiv8,
    content:
      "Learn JavaScript, React, React Native and managing component state with redux",
    year: "2019"
  },
  {
    text: "Go Academy x BNCC",
    logo: GoAcademy,
    content:
      "Understanding of TDD, Clean Code, OOP Principles, CI/CD, and Agile Principles",
    year: "2019"
  },
  {
    text: "Responsive Web Design",
    logo: FreeCodeCamp,
    content: "Learn HTML, CSS for Developing Responsive Web Design",
    year: "2019"
  },
  {
    text: "The Fundamentals of Digital Marketing",
    logo: GoogleDigitalGarage,
    content:
      "Learn Fundamentals of Digital Marketing, How to Optimize Website on Google Search Engine",
    year: "2019"
  }
];

const CertificationList = () => {
  return data.map((item, index) => {
    return <CertificationCard key={index} {...item} />;
  });
};

export default CertificationList;
