import React, { useState } from "react";
import checkCircleSvgBlue from "./../../../assets/icon/checkCircleBlue.svg";
import checkCircleSvgGrey from "./../../../assets/icon/checkCircleGrey.svg";
import "./ExperienceCard.css";

const ExperienceCard = ({ title, location, content }) => {
  const [toggle, setToggle] = useState(false);
  const containerRef = React.createRef();
  const titleRef = React.createRef();

  function handleMouse(e) {
    containerRef.current.classList.toggle("card-experience-container-open");
    titleRef.current.classList.toggle("card-experience-title-open");
    setToggle(!toggle);
  }

  return (
    <div
      className="card-experience-container"
      ref={containerRef}
      onClick={(e) => handleMouse(e)}
    >
      <div className="card-experience-top">
        {toggle ? (
          <img
            src={checkCircleSvgBlue}
            alt="check"
            className="checkCircle experience-check"
          />
        ) : (
          <img
            src={checkCircleSvgGrey}
            alt="check"
            className="checkCircle experience-check"
          />
        )}
        <div className="card-experience-right">
          <div className="card-experience-title" ref={titleRef}>
            {title}
          </div>
          <div className="card-experience-subtitle">{location}</div>
        </div>
      </div>
      <div className="card-experience-bottom">{content}</div>
    </div>
  );
};

export default ExperienceCard;
