import React, { useState } from "react";
import checkCircleSvgBlue from "./../../../assets/icon/checkCircleBlue.svg";
import checkCircleSvgGrey from "./../../../assets/icon/checkCircleGrey.svg";
import "./CertificationCard.css";

const CertificationCard = ({ text, logo, content, year }) => {
  const [toggle, setToggle] = useState(false);
  const containerRef = React.createRef();

  function handleMouse(e) {
    const container = containerRef.current;
    container.classList.toggle("card-certification-container-open");
  }
  return (
    <div
      className="card-certification-container"
      // onMouseDown={(e) => handleMouse(e)}
      // onMouseUp={(e) => handleMouse(e)}
      // onClick={() => setToggle(!toggle)}
      // ref={containerRef}
    >
      <div className="card-certification-top">
        <div className="card-certification-left">
          <img src={logo} alt="logo" className="certification-logo" />
          <div>{text}</div>
        </div>
      </div>
      <div className="card-certification-content">{content}</div>
      <div className="card-certification-bottom">
        <div>{year}</div>
      </div>
    </div>
  );
};

export default CertificationCard;
